.modal
	position: fixed
	z-index: 10
	width: 100vw
	height: 100vh
	top: 0
	left: 0
	display: flex
	align-items: center
	justify-content: center
	overflow: auto
	background: rgba(#000, .4)
	opacity: 0
	visibility: hidden
	background: #0009
	backdrop-filter: blur(4px)
	background-repeat: no-repeat
	background-size: cover
	background-position: center center
	&.active
		opacity: 1
		visibility: visible
		.modal-content
			opacity: 1
			transform: translateY(0)
	&-content
		padding: 2rem
		padding-top: 2rem
		background: rgba(0,0,0,.75)
		max-width: 768px
		opacity: 0
		transform: translateY(-250px)
		position: relative
		transition: all .25s ease
		border-radius: 4px
		backdrop-filter: blur(4px)
		max-width: 771px
		width: 100%
		&.modal_image
			max-width: 100%
			background: 0 0
			backdrop-filter: none
		.box
			p
				text-align: center
	&__close
		position: absolute
		top: 10px
		right: 10px
		width: 50px
		height: 50px
		// background: #fff
		display: flex
		align-items: center
		justify-content: center
		cursor: pointer
		display: flex   
		svg
			fill: #fff
			width: 26px
			height: 26px
			pointer-events: none
	&__video
		width: 100%
		padding-top: 56.25%
		position: relative
		iframe
			position: absolute
			width: 100%
			height: 100%
			top: 0
			left: 0
	&__img
		max-width: 100%
		text-align: center
		img
			max-width: 100%
			max-height: calc(100vh - 4rem)
			height: auto
			width: auto
	&__btn-prev, &__btn-next
		position: absolute
		top: 50%
		transform: translateY(-50%)
		cursor: pointer
		width: 40px
		height: 40px
		display: flex
		align-items: center
		justify-content: center
		&::before
			content: ''
			width: 28px
			height: 28px
			display: block
			position: relative
			background-image: url('data:image/svg+xml;utf8,<svg viewBox="0 0 18 18" xmlns="http://www.w3.org/2000/svg"><path fill="%23e50914" d="M11.56 5.56L10.5 4.5 6 9l4.5 4.5 1.06-1.06L8.12 9z"></path></svg>')
			background-repeat: no-repeat
			background-position: center center
			background-size: contain
	&__btn-prev 
		left: 5px
	&__btn-next
		right: 5px
		&:before
			transform: rotate(180deg)
	&__pagination
		position: absolute
		bottom: 5px
		width: 100%
		text-align: center
		left: 0
		font-size: 12px
