.main
	&-grid
		display: grid
		grid-template-columns: repeat(auto-fill, minmax(240px, 1fr) )
		grid-gap: 25px
		+media-breakpoint-down(xs)
			grid-template-columns: repeat(2,1fr )
			grid-gap: 15px
		@media screen and (max-width:375px)
			grid-gap: 10px
		&+.pagination
			margin-top: 3rem
			padding-bottom: 3rem
.card
	text-align: center
	display: flex
	flex-direction: column
	overflow: hidden
	position: relative
	box-shadow: 0 0 20px #000
	// padding-bottom: 15px
	border-radius: 5px
	padding-bottom: 3px
	background: rgba($dark, .85)
	&:hover
		.card__img
			img
				transform: scale(1.05)
	&__img
		// margin-bottom: auto
		overflow: hidden
		position: relative
		padding-bottom: 150%
		-webkit-mask-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, black), color-stop(0.35, black), color-stop(0.5, black), color-stop(0.65, black), color-stop(0.85, rgba(0, 0, 0, 0.6)), color-stop(1, transparent))
		img
			width: 100%
			// height: auto
			height: 100%
			object-fit: cover
			object-position: top
			position: absolute
			top: 0
			left: 0
			transition: all .25s ease
	&__name
		margin-top: auto
		font-size: 15px
		line-height: 1.2em
		min-height: 50px
		padding: 10px
		padding-bottom: 7px
		display: flex
		align-items: center
		justify-content: center
		+media-breakpoint-down(xs)
			font-size: 14px
		@media screen and (max-width:360px)	
			padding: 5px
	&__score
		position: absolute
		top: 0
		right: 0
		padding: 3px 5px
		font-size: 14px
		z-index: 3
		color: $text
		&:before
			position: absolute
			content: ''
			width: 100%
			height: 100%
			top: 0
			left: 0
			background: $darkred
			opacity: 0.9
			z-index: -1
	&__date
		font-weight: 400
		font-size: 14px
		padding-bottom: 7px
		opacity: 0.65
	&-controls
		position: absolute
		width: 100%
		height: 100%
		top: 0
		left: 0
		display: flex
		align-items: center
		justify-content: center
		&>*
			position: absolute
			z-index: 4
			transition: all .25s ease
			transform-style: preserve-3d
		&__add
			&::after
				position: absolute
				content: ''
				width: 100%
				height: 3px
				background: $red
				bottom: 0
				left: 0
				border-radius: 3px
		&__added
			width: 50%
			height: 50%
			&::before
				position: absolute
				content: ''
				width: 100%
				height: 100%
				top: 0
				left: 0
				background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 50 50"><path fill="%23e50914" d="M40.267 14.628L20.974 33.921l-9.293-9.293a.999.999 0 1 0-1.414 1.414l10 10a.997.997 0 0 0 1.414 0l20-20a.999.999 0 1 0-1.414-1.414z"/></svg>')
				background-repeat: no-repeat
				background-position: center center
				background-size: contain
				opacity: 0
				transform: scale(0)
				transition: all .25s ease
	&.card_remove
		cursor: pointer
		&::before
			position: absolute
			content: ''
			top: 0
			left: 0
			right: 0
			bottom: 0
			background: $dark
			z-index: 2
			opacity: 0.55
			transition: all .25s ease
		&.is-checked
			&::before
				opacity: 0.73
				box-shadow: inset 0px 0px 14px 7px #000
			.card-controls
				&__add
					transform: scale(0)
					opacity: 0
				&__added
					&::before		
						opacity: 1
						transform: scale(1)
.genres
	&-list
		display: flex
		flex-wrap: wrap
		margin-bottom: 2rem
		margin-right: -5px
		&>li
			margin-right: 5px
			margin-bottom: 5px
			color: $black
			line-height: 1em
			background: #fff
			padding: 5px
			border-radius: 3px
			font-weight: 400
			font-size: 14px
			cursor: pointer
			&.is-active
				background: $darkred
				color: #fff