.lists
	&-grid
		display: grid
		grid-template-columns: repeat(auto-fill, minmax(450px, 1fr) )
		grid-gap: 15px
		padding-bottom: 4rem
		+media-breakpoint-down(xs)
			grid-template-columns: repeat(1, 1fr)
	&-item
		background-repeat: no-repeat
		background-position: center center
		background-size: auto 100%
		text-align: center
		padding: 25% 14%
		position: relative
		background-color: $darkred
		border-radius: 5px
		&:hover
			background-size: auto 105%
		&__name
			font-size: 24px
			font-weight: 700
			color: #f6f6f6
		&__items
			color: #f6f6f6
		&>*
			position: relative
			z-index: 2
		&:before
			position: absolute
			content: ''
			width: 100%
			height: 100%
			top: 0
			left: 0
			background: $dark
			opacity: 0.4
			z-index: 1
			pointer-events: none
.list
	&-section
		padding: 9% 0
		background-repeat: no-repeat
		background-position: center center
		background-size: cover
		position: relative
		&>*
			position: relative
			z-index: 2
		&:before
			position: absolute
			content: ''
			width: 100%
			height: 100%
			top: 0
			left: 0
			background: $dark
			opacity: 0.8
			z-index: 1
			pointer-events: none
	&-controls
		margin-bottom: 2rem
	&-empty
		font-size: 26px
		text-align: center
		line-height: 1.25em
		div
			margin-top: 1rem