.nav
	&-w
		// display: flex
		display: grid
		grid-template-columns: repeat(3,1fr )
		padding: 42px 0
		align-items: center
		gap: 10px
		+media-breakpoint-down(md)
			// display: flex
			
	&-logo
		margin: 0 auto
		&__mobile
			display: none
		img
			max-width: 140px
			height: auto
	&-auth
		display: flex
		justify-content: flex-end
		min-width: 178px
		height: 60px
		align-items: center
	&-burger
		min-width: 30px
		width: 30px
		height: 35px
		display: flex
		align-items: center
		display: none
		&.is-close
			span				
				background: rgba($red, 0)
				&:before
					transform: rotate(-45deg)
					top: 0
				&::after
					transform: rotate(45deg)
					top: 0
		span
			height: 3px
			width: 100%
			background: $red
			position: relative
			border-radius: 3px
			transition: all .25s ease
			&:before, &:after
				position: absolute
				content: ''
				width: 100%
				height: inherit
				background: $red
				border-radius: inherit
				left: 0
				transition: all .25s ease
			&:before
				top: -7px
			&:after
				top: 7px
	&-search
		&__show, &__close
			display: none
	&-login
		display: flex
		align-items: center
		overflow: hidden
		&:hover
			+media-breakpoint-up(lg)
				.nav-login
					&__avatar
						left: calc(100% - 60px)
					&__body
						.btn
							opacity: 1
							left: calc(100% - 80px)
							pointer-events: all
							transform: translateX(-100%)
							transition-delay: 0.1s
					&__name
						opacity: 0
						transform: translateX(100%)				
		&__avatar
			border-radius: 50%
			overflow: hidden
			max-width: 60px
			min-width: 60px
			position: relative
			transition: all .25s ease
			left: 0
			div
				width: 100%
				padding-bottom: 100%
				border-radius: inherit
				position: relative
				// background: $darkred
			img
				width: 100%
				height: auto
				border-radius: inherit
				border: 2px solid #48494b
				position: absolute
				top: 0
				left: 0
		&__name
			margin-left: 20px
			font-size: 14px
			transition: all .25s ease
			min-width: 90px
		&__body
			position: relative
			height: 100%
			display: flex
			align-items: center
			.btn
				position: absolute
				opacity: 0
				pointer-events: none
				left: -100%
+media-breakpoint-down(md)
	.nav		
		&-w		
			padding: 9px 0
			grid-template-columns: 55px 40px auto 94px
			height: 80px
		&-burger
			display: flex
			margin-right: 25px
		&-logo
			&__mobile
				// display: block
			img
				// max-width: 32px
				&:not(.nav-logo__mobile)
					// display: none
		&-search
			.form
				position: fixed
				display: flex
				width: 100vw
				height: 100vh
				align-items: center
				justify-content: center
				flex-direction: column
				inset: 0 0 0 0
				background: rgba($dark, 0.7)
				transition: all .25s ease
				opacity: 0
				pointer-events: none
				z-index: 3
				// left: 50%
				// transform: translateX(-50%)
				&.is-show
					opacity: 1
					pointer-events: all
					.form__search, .nav-search__close
						transition-delay: .25s
						transform: translateY(0px)
						opacity: 1						
				.form__search
					width: 50%
					transform: translateY(-50px)
					transition: all .25s ease
					opacity: 0
					.form__group
						width: 100%
						.form__border
							transform: scaleX(1)
			&__show
				display: flex
			&__close
				display: flex
				margin-bottom: 3rem
				opacity: 0
				transform: translateY(-50px)
				transition: all .25s ease
		&-auth
			min-width: 94px
			&>.btn
				position: relative
				font-size: 0
				background: 0 0
				width: 24px
				height: 24px
				padding: 0
				pointer-events: all
				&:before
					display: none
				&:after
					min-width: 100%
					content: ''
					width: 100%
					height: 100%
					position: absolute
					left: 0
					top: 0
